/* 
=====================================================================
Accordion
=====================================================================
*/
.producer-member-list {
  padding: 0 20px;
}
.producer-member-list .footnote {
  max-width: 600px;
  line-height: 1.6;
  margin: 0 auto;
  font-size: 14px;
}
.producer-member-list .accordion {
  background-color: #fefffa;
  max-width: 600px;
  margin: 50px auto;
  border-top: 6px solid #33A99D;
  line-height: 1.6;
  box-shadow: 0 23px 40px rgba(0, 0, 0, 0.2);
  border-radius: 6px;
}
.producer-member-list .accordion__item {
  border-bottom: 1px solid #dce7eb;
}
.producer-member-list .accordion__title {
  padding: 15px 15px 15px 40px;
  display: block;
  position: relative;
  font-weight: 400;
}
.producer-member-list .accordion__title:before {
  font-family: FontAwesome;
  content: "\f055";
  font-size: 20px;
  position: absolute;
  left: 15px;
  top: 12px;
  color: #33A99D;
}
.producer-member-list .accordion__title h5 {
  border-bottom: 1px solid #fefffa;
  display: inline-block;
}
.producer-member-list .accordion__title:hover,
.producer-member-list .accordion__title:focus {
  cursor: pointer;
  outline: none;
}
.producer-member-list .accordion__title:hover h5,
.producer-member-list .accordion__title:focus h5 {
  border-bottom-color: #a8bdc4;
  display: inline-block;
}
.is-expanded .producer-member-list .accordion__title:before {
  content: "\f056";
}
.producer-member-list .accordion__content-inner {
  padding: 0 40px 10px 40px;
}
.producer-member-list .accordion__content-inner ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
.producer-member-list .accordion__content-inner ul li {
  font-size: 14px;
  list-style: none;
  padding-left: 20px;
  position: relative;
  padding-bottom: 10px;
}
.producer-member-list .accordion__content-inner ul li:before {
  background-color: #006cb4;
  content: "";
  height: 100%;
  left: 3.5px;
  position: absolute;
  top: calc(0.5em - 4px);
  width: 1px;
}
.producer-member-list .accordion__content-inner ul li:after {
  background-color: #fff;
  border: 1px solid #006cb4;
  border-radius: 50%;
  box-sizing: border-box;
  content: "";
  display: inline-block;
  flex-shrink: 0;
  height: 7px;
  left: 0;
  position: absolute;
  top: calc(5px - 4px);
  width: 8px;
}
.producer-member-list .accordion__content-inner ul li:last-of-type:before {
  background-color: transparent;
}
.producer-member-list .accordion__content {
  transition: height 0.3s ease-out;
  height: 0;
  overflow: hidden;
}
